<template>
  <div class="list">
    <div class="clearfix list-button">
      <div class="pull-left">
        <h4 class="list-button-text">合同基本信息：</h4>
      </div>
      <div class="pull-right" style="margin-right: 10px">
        <el-button   @click="">删除</el-button>
        <el-button   @click="">保存</el-button>
      </div>
    </div>
    <div class="clearfix list-center">
      <!--<h-table></h-table>-->
      <el-col :span="12">
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="120px" class="demo-ruleForm">
          <el-form-item label="合同编号" prop="name">
            <el-input v-model="ruleForm.name"></el-input>
          </el-form-item>
          <el-form-item label="项目名称" prop="name">
            <el-input v-model="ruleForm.name"></el-input>
          </el-form-item>
          <el-form-item label="客户名称" prop="name">
            <el-input v-model="ruleForm.name"></el-input>
          </el-form-item>
          <el-form-item label="签署时间" required>
            <el-date-picker v-model="ruleForm.date1" type="datetime" placeholder="选择日期时间">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="合同金额" prop="name">
            <el-input v-model="ruleForm.name"></el-input>
          </el-form-item>
          <el-form-item label="执行状态" prop="region">
            <el-select v-model="ruleForm.name"  placeholder="请选择执行状态" style="display: block">
              <el-option
                v-for="item in stateOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="渠道" prop="name">
            <el-input v-model="ruleForm.name"></el-input>
          </el-form-item>
          <el-form-item label="联系人" prop="name">
            <el-input v-model="ruleForm.name"></el-input>
          </el-form-item>
          <el-form-item label="联系人手机号" prop="name">
            <el-input v-model="ruleForm.name"></el-input>
          </el-form-item>
          <el-form-item label="销售负责人" prop="name">
            <el-input v-model="ruleForm.name"></el-input>
          </el-form-item>
        </el-form>
      </el-col>
    </div>
    <div class="clearfix list-button">
      <div class="pull-left">
        <h4 class="list-button-text">备注：</h4>
      </div>
    </div>
    <div class="list-center-bottom" style="height: 140px;border: 1px solid #dfdfdf;background: #fafafa">
      <el-input type="textarea" ></el-input>
    </div>
  </div>
</template>
<script>
  import HTable from '@/components/layout/table/HTable'
  export default {
    name: 'chanceDetail',
    components: {
      HTable
    },
    data () {
      return {
        ruleForm: {
          name: '',
          region: '',
          date1: ''
        },
        rules: {
          name: [
            { required: true, message: '请输入', trigger: 'blur' }
          ],
          region: [
            { required: true, message: '请选择', trigger: 'change' }
          ]
        },
        stateOptions: [{
          value: 'state1',
          label: '全部'
        }, {
          value: 'state2',
          label: '未开始'
        }, {
          value: 'state3',
          label: '执行中'
        }, {
          value: 'state4',
          label: '已交付'
        }, {
          value: 'state5',
          label: '已关闭'
        }],
        stateValue: ''
      }
    },
    watch: {
      /**
       * @params 监听参数变化重新获取数据
       * */
      params: {
        handler (val) {
          this.getData(val)
        },
        deep: true
      }
    },
    computed: {
      state () {
        return this.$store.state.app
      }
    },
    methods: {
      /**
       * @params:category 分类 page：页码 limit:条数
       * */
      getData (params) {
      },
      /**
       * @on-change 页码改变的回调，返回改变后的页码
       * */
      pageChange (page) {
        this.params.page = page
      },
      /**
       * @on-page-size-change 切换每页条数时的回调，返回切换后的每页条数
       * */
      PageSizeChange (limit) {
        this.params.limit = limit
      },
      /**
       * 表格对应操作方法
       * @show 查看
       * @remove 删除
       * @edit
       */
      remove (index) {
        this.listData.splice(index, 1)
      },
      edit (index) {
        this.editModal = true
        this.currIndex = index
        if (index === -1) { // 新增
          this.currDate = {
            createdAt: '',
            desc: '',
            publishedAt: '',
            type: '',
            used: true,
            who: '',
            url: 'c.fwone.com'
          }
        } else { // 编辑
          this.currDate = this.listData[index]
        }
      },
      /**
       * 多选
       * selection：已选项数据 row：刚选择的项数据
       */
      onSelect (selection, row) {
        // console.log(selection,row)
      },
      onSelectionChange (selection) {
        this.selection = selection
      }
    },
    created () {
      this.getData(this.params)
    },
    mounted () {
    }
  }
</script>



